	content: '';
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;
	background: #fff;
	z-index: -1;
}

.GlowCard_box__2lTse::after {
	content: '';
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;
	background: #fff;
	z-index: -2;
	-webkit-filter: blur(40px);
	        filter: blur(40px);
}

.GlowCard_box__2lTse:hover {
	box-shadow: 0px 0px 10px #FE13BE, 0px 0px 15px #000052, 0px 0px 15px #8E32FF;
	transition: 0.2s ease-in;
	-webkit-transition: box-shadow 0.2s ease-in;
}

.GlowCard_box__2lTse::before,
.GlowCard_box__2lTse::after 
{
    background: linear-gradient(var(--degree),#FE13BE,#000052,#8E32FF);
    
}

