.login-register-container {
    background-color: #060c21 !important;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    min-height: 480px;
}

.form-container {
    display: flex !important;
    flex-direction: column !important;
    height: 50vh;
    transition: all 0.6s ease-in-out;
    background-color: #060c21 !important;
    align-items: center;
    justify-content: center;
}

.sign-in-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around !important;
    align-items: center;
    left: 0;
    width: 50%;
    opacity: 1;
    animation: toright 1.5s linear;
    /* animation: show 1.5s; */
    z-index: 2 !important;
    height: 72vh !important;
    position: absolute;
    top: 20%;
}


.sign-in-container * {
    z-index: 2 !important;
}

.login-register-container.right-panel-active .sign-in-container {
    left: 50%;
    opacity: 0;
    animation: toleft 1.5s linear;
    /* animation: show 1.5s; */
}

.sign-up-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around !important;
    align-items: center;
    opacity: 0;
    right: 50%;
    width: 50%;
    animation: toleft 1.5s linear;
    z-index: 1 !important;
    position: absolute;
    top: 15%;
    height: 80vh !important;
}

.login-register-container.right-panel-active .sign-up-container {
    right: 0;
    opacity: 1;
    animation: toright 1.5s linear;
    /* animation: show 1.5s linear; */
    z-index: 5 !important;
}

@keyframes toleft {
    0% {
        opacity: 1;
        right: 0;
    }

    25% {
        opacity: 0;
        right: 15%;
    }

    100% {
        opacity: 0;
        right: 50%
    }
}


@keyframes toright {
    0% {
        opacity: 0;
        right: 50%;
    }

    25% {
        opacity: 0;
        right: 15%;
    }

    50% {
        opacity: 0.5;
